<template>
    <div class="activity-register-page">
        <navigation-bar
            class="register-nav-bar"
            :pageName="title"
            :border="false"
            @onLeftClick="onBack"
        />
        <div class="register-content">
            <div class="register-tip">
                <div class="acivity-name">
                    <span>{{activityInfo.DETAIL_NAME}}</span>
                </div>
                <div class="register-progress">
                    <progress :value="activitySignupPercentage(activityInfo)" max="100"></progress>
                    <div>{{activityInfo.ENROL_CNTS}}/{{activityInfo.LIMIT_MAX}}人</div>
                </div>
            </div>
            <div class="grayBar"></div>
            <van-tabs
            v-model="activeTab"
            swipeable
            sticky
            :offset-top="44"
            color="#00d8d1">
                <van-tab title="未签到">
                    <van-index-bar :index-list="nosigninIndex">
                        <div v-for="index in nosigninIndex" :key="index">
                            <van-index-anchor :index="index" />
                            <div class="register-cell" v-for="(ele, key) in nosigninData[index]" :key="key">
                                <div class="owner-name">{{ele.OWNER_NAME}}</div>
                                <div class="owner-info" :style="{width: (activityInfo.CLOSE_SIGN_FLAG==0 ? '176px' : '276px')}">
                                    <div class="owner-address">{{ele.BUILD_NAME}}栋-{{ele.UNIT_NO}}单元-{{ele.ROOM_NAME}}室</div>
                                    <div class="owner-signinnumber">报名人数：{{ele.ENROL_CNT}}</div>
                                    <a class="owner-phonenumber" :href="'tel:' + ele.PHONE">
                                        <div>{{ele.PHONE}}</div>
                                        <van-icon name="phone-o" size="26px" color="#FF0000"/>
                                    </a>
                                </div>
                                <div class="action-button" v-show="activityInfo.CLOSE_SIGN_FLAG==0 ? true : false" @click="signIn(ele.OWNER_CODE)">签到</div>
                            </div>
                        </div>
                    </van-index-bar>
                    <div class="no-list" v-show="nosigninIndex.length == 0">
                        <img :src="noSourceImg" alt="">
                        <div>暂无未签到信息</div>
                    </div>
                </van-tab>

                <van-tab title="已签到">
                    <van-index-bar :index-list="signinIndex">
                        <div v-for="index in signinIndex" :key="index">
                            <van-index-anchor :index="index"/>
                            <div class="register-cell" v-for="(ele, key) in signinData[index]" :key="key">
                                <div class="owner-name">{{ele.OWNER_NAME}}</div>
                                <div class="owner-info" :style="{width: (activityInfo.CLOSE_SIGN_FLAG==0 ? '176px' : '276px')}">
                                    <div class="owner-address">{{ele.BUILD_NAME}}栋-{{ele.UNIT_NO}}单元-{{ele.ROOM_NAME}}室</div>
                                    <div class="owner-signinnumber">报名人数：{{ele.ENROL_CNT}}</div>
                                    <a class="owner-phonenumber" :href="'tel:' + ele.PHONE">
                                        <div>{{ele.PHONE}}</div>
                                        <van-icon name="phone-o" size="26px" color="#FF0000"/>
                                    </a>
                                </div>
                                <div class="action-button" v-show="activityInfo.CLOSE_SIGN_FLAG==0" @click="cancel(ele.OWNER_CODE)">取消</div>
                            </div>
                        </div>
                    </van-index-bar>
                    <div class="no-list" v-show="signinIndex.length == 0">
                        <img :src="noSourceImg" alt="">
                        <div>暂无签到信息</div>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
    </div>
</template>

<script>
  import {
    getActivityRegisterListService,
    updateActivityListStatusService
  } from "../../services/activityService";
  import {store, storage} from "../../utils/store";

  import Vue from 'vue';
  import {Toast, Tab, Tabs, IndexBar, IndexAnchor, Icon} from 'vant';
  Vue.use(Toast).use(Tab).use(Tabs).use(IndexBar).use(IndexAnchor).use(Icon);
  import NavigationBar from "../title/NavigationBar";

  // 暂无数据图片
  import noSourceImg from '@/assets/imgs/workPlan/noSource.png'

  export default {
    name: "activityRegisterList",
    components: { NavigationBar },
    created() {
        this.activityInfo = this.$route.params;
        // 加载页面数据
        this.loadPageData();
    },
    data() {
      return {
        active: 0,
        title: '报名列表',
        activeTab: 0,

        // 活动数据，上个页面传入
        activityInfo:{},
        // 未签到数据
        nosigninIndex: [],
        nosigninData: {},
        // 签到数据
        signinData: {},
        signinIndex: [],

        //图片
        noSourceImg: noSourceImg,
      };
    },
    methods: {
        loadPageData() {

            let requestData = {
                DETAIL_CODE: this.activityInfo.DETAIL_CODE
            };
            getActivityRegisterListService(requestData)
            .then(noteData => {
                if (noteData && noteData.length > 0) {
                    //数据解析
                    this.regiseterDataParse(noteData);
                }
                else {
                    Toast.fail('无报名信息');
                }
            })
            .catch(error => {
                Toast.fail(error);
            });
        },
        // 接口返回报名信息解析
        regiseterDataParse(registerData) {
            this.signinIndex = [];
            this.signinData = [];
            this.nosigninIndex = [];
            this.nosigninData = [];
            for (let index = 0; index < registerData.length; index++) {
                let element = registerData[index];
                let firstLetter = this.getPinYinFirstLetter(element.OWNER_NAME_PINYIN);
                if (element.SCENE_FLAG && element.SCENE_FLAG == 1) {
                    /// 已签到
                    //填充索引并去重
                    if (this.signinIndex.indexOf(firstLetter) === -1) {
                        this.signinIndex.push(firstLetter);
                    }
                    if (this.signinData[firstLetter] == undefined) {
                        this.signinData[firstLetter] = [];
                    }
                    // 根据索引分类数据
                    this.signinData[firstLetter].push(element);
                }
                else {
                    /// 未签到
                    //填充索引并去重
                    if (this.nosigninIndex.indexOf(firstLetter) === -1) {
                        this.nosigninIndex.push(firstLetter);
                    }

                    if (this.nosigninData[firstLetter] == undefined) {
                        this.nosigninData[firstLetter] = [];
                    }
                    // 根据索引分类数据
                    this.nosigninData[firstLetter].push(element);
                }
            }
            // 排序，并把'#'放最后
            this.signinIndex.sort();
            this.nosigninIndex.sort();
            let tmpIndex = this.signinIndex.indexOf('#');
            if (tmpIndex > -1) {
                this.signinIndex.splice(tmpIndex, 1);
                this.signinIndex.push('#');
            }
            tmpIndex = this.nosigninIndex.indexOf('#');
            if (tmpIndex > -1) {
                this.nosigninIndex.splice(tmpIndex, 1);
                this.nosigninIndex.push('#');
            }
        },
        // 获取拼音的首字母
        getPinYinFirstLetter(pingyin) {
            let firstLetter;
            if (pingyin
                && pingyin != undefined
                && pingyin.length > 0) {
                let reg=/^[A-Za-z]+$/;
                firstLetter = pingyin[0];
                firstLetter = reg.test(firstLetter) ? firstLetter.toUpperCase(): '#';
            }
            else {
                firstLetter = '#'
            }
            return firstLetter;
        },
        // 点击签到按钮
        signIn(OWNER_CODE) {
            var stateFlag={
                SCENE_FLAG:'1',
                DETAIL_CODE: this.activityInfo.DETAIL_CODE,/*场次代码*/
                USER_CODE: store.get("userInfo").USER_CODE,
                OWNER_CODE: OWNER_CODE,
            };
            updateActivityListStatusService(stateFlag).then(res => {
                if(res.success){
                    this.$toast('签到成功');
                    this.activeTab = 1;
                    this.loadPageData()
                }else{
                    Toast.fail('签到失败');
                }
            }, error => {
                Toast.fail(error);
            })
        },
        // 点击取消按钮
        cancel(OWNER_CODE) {
            var stateFlag={
                SCENE_FLAG:'0',
                DETAIL_CODE: this.activityInfo.DETAIL_CODE,/*场次代码*/
                USER_CODE: store.get("userInfo").USER_CODE,
                OWNER_CODE: OWNER_CODE,
            };
            updateActivityListStatusService(stateFlag).then(res => {
                if(res.success){
                    this.$toast('取消成功');
                    this.activeTab = 0;
                    this.loadPageData()
                }else{
                    Toast.fail('取消失败');
                }
            }, error => {
                Toast.fail(error);
            })
        },
        onBack() {
            history.back();
        }
    },
    computed: {
        // 活动报名人数百分比计算
        activitySignupPercentage() {
          return function(item) {
            let totalPeople = item.LIMIT_MAX;//拿到总人数
            let signPeople = item.ENROL_CNTS;//拿到报名人数
            let percentage = Math.round(Number(signPeople)/Number(totalPeople) * 10000) / 100.00;
            return percentage;
          }
        },
    }
  };
</script>

